body, ul {
  margin: 0;
  padding: 0;
}
img {
  display: block;
}
ul {
  list-style: none;
}
#Sidebar {
  position: fixed;
  top: 0;
  left: -240px;
  width: 240px;
  height: 100vh;
  background-color: #fff;
  border-right: solid 1px #efefef;
  -webkit-transition: left .3s ease-out;
  -o-transition: left .3s ease-out;
  transition: left .3s ease-out;
}
#Sidebar.Active {
  left: 0;
}
#Mask.Active {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .3);
}
#Clear,
#Export {
  position: fixed;
  bottom: 20px;
  right: 24px;
}
#Clear {
  right: 72px;
}
#Menu {
  position: absolute;
  top: 20px;
  right: -50px;
}
#Clear:hover,
#Export:hover,
#Menu:hover {
  cursor: pointer;
}
.Close-Icon {
  width: 24px;
}
.Menu-Icon {
  width: 22px;
}
.Export-Icon {
  width: 26px;
}
.Clear-Icon {
  width: 20px;
}
.Label {
  text-align: center;
  font-size: 20px;
  margin: 40px 0 20px;
  color: #ccc;
}
#Palette {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}
#Palette li {
  -ms-flex: 0 0 24px;
      flex: 0 0 24px;
  height: 24px;
  border-radius: 50%;
  cursor: pointer;
}
#Palette li.Current {
  border-radius: 0;
}



